<template>
  <div class="lvb">
    <div class="cutscenes" v-if="!videoLoaded">
      <img :src="loading" />
    </div>
    <div
      v-if="IsLoaded && Videos && Videos.length > 0"
      :class="'vw_' + Videos.length"
    >
      <div
        :id="'player' + (index + 1)"
        v-for="(video, index) in Videos"
        :key="index"
      >
        <Player
          :id="video.id"
          @click="Swap"
          ref="player"
          :Index="index"
          :ShowRoomName="false"
          :ShowPatrolBtn="ShowPatrolBtn"
          :ClsInfo="
            video.DeviceInfo
              ? {
                  hasCls: false,
                  IP: video.DeviceInfo.IP,
                  Channel: video.DeviceInfo.Channel,
                  IP: video.DeviceInfo.IP,
                  Port: video.DeviceInfo.Port,
                  BrandCode: video.DeviceInfo.BrandCode,
                  ModelCode: video.DeviceInfo.ModelCode,
                  User: video.DeviceInfo.User,
                  Pwd: video.DeviceInfo.Pwd,
                  DeviceName: video.DeviceType === '4' ? '': '',
                  Site: video.DeviceInfo.Site ? video.DeviceInfo.Site : '0',
                  ClsRoomName: ClsRoomName,
                  Classes: ClsInfo ? ClsInfo.Classes : '',
                  CourseName:
                    ClsInfo && ClsInfo.ScheduleInfo
                      ? ClsInfo.ScheduleInfo.CSTitle
                      : '',
                  Teachers: ClsInfo ? ClsInfo.Theachers : '',
                  CSLesson:
                    ClsInfo && ClsInfo.ScheduleInfo
                      ? ClsInfo.ScheduleInfo.CSLesson
                      : '',
                  LessonClsTime: ClsInfo ? ClsInfo.LessonClsTime : '',
                  LessonOutTime: ClsInfo ? ClsInfo.LessonOutTime : '',
                  ID: video.id,
                  ModelCode: video.ModelCode
                }
              : { ID: video.id }
          "
        />
      </div>
    </div>
    <el-carousel trigger="click" v-else>
      <el-carousel-item v-for="item in 3" :key="item">
        <img :src="bgStu" v-if="item === 1" />
        <img :src="bgThc" v-if="item === 2" />
        <img :src="bgWin" v-if="item === 3" />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import $ from "jquery";
// import h5media from "@/scripts/h5media";
export default {
  props: {
    Videos: { type: Array, default: () => [] },
    ClsInfo: { type: Object, default: () => null },
    ClsRoomName: { type: String, default: "" },
    ShowPatrolBtn: { type: Boolean, default: true },
  },
  watch: {
    Videos(newVal, oldVal) {
      if (oldVal !== newVal) {
        this.IsLoaded = false;
        this.Dispose();
        this.$nextTick(() => {
          this.IsLoaded = true;
          this.videoLoaded = true;
        });
      }
    },
    $route() {
      this.videoLoaded = false;
      this.Dispose();
    },
  },
  components: {
    Player: () => import("@/components/Player/PatrolPlayer"),
  },
  data() {
    return {
      loading: require("@/assets/img/loading.gif"),
      bgStu: require("@/assets/img/bgStu.png"),
      bgThc: require("@/assets/img/bgThc.png"),
      bgWin: require("@/assets/img/bgWin.png"),
      IsLoaded: false,
      videoLoaded: false,
      // videoLoadedCount: 0
    };
  },
  updated() {
    // this.LoadVideo();
  },
  beforeDestroy() {
    this.Dispose();
    console.log("");
  },
  methods: {
    // 
    Dispose() {
      if (this.$refs.player) {
        this.$refs.player.forEach((player) => {
          player.Dispose();
        });
      }
    },
    // 
    Swap(e) {
      // console.log(e.target.className);
      if (e.target.className === "player") {
        const index = parseInt(e.target.parentElement.id.substring(6)) - 1;

        const players = $(".lvb >div >div");

        const sourceDiv = players[index];
        const sourceVideo = sourceDiv.firstChild;

        if ($(sourceVideo).find("video")[0].muted) {
          $(sourceVideo).find("video")[0].muted = false;
        }

        if (index !== 0) {
          const targetDiv = players[0];
          const targetVideo = targetDiv.firstChild;

          $(targetDiv).find("video")[0].muted = true;

          $(sourceVideo).appendTo(targetDiv);
          $(targetVideo).appendTo(sourceDiv);
        }
      }
    },
    // 
    PTZ(cmdIndex, speedSlider, stop) {
      let cmd = "";
      switch (cmdIndex) {
        case 1: {
          cmd = "Up_Left";
          break;
        }
        case 2: {
          cmd = "Up";
          break;
        }
        case 3: {
          cmd = "Up_Right";
          break;
        }
        case 4: {
          cmd = "Left";
          break;
        }
        case 6: {
          cmd = "Right";
          break;
        }
        case 7: {
          cmd = "Down_Left";
          break;
        }
        case 8: {
          cmd = "Down";
          break;
        }
        case 9: {
          cmd = "Down_Right";
          break;
        }
        case 10: {
          cmd = "ZOOM_IN";
          break;
        }
        case 11: {
          cmd = "ZOOM_OUT";
          break;
        }
      }
      const players = $(".lvb >div >div");
      const targetDiv = players[0];
      const ip = $(targetDiv)
        .find(".clsInfo .schInfo >div:first")
        .text()
        .substring(3);
      window.hub.invoke("PTZ", ip, cmd, speedSlider, stop);
    },
  },
};
</script>

<style lang="less">
.lvb {
  width: 100%;
  height: 100%;
  position: relative;
  @BorderColor: rgba(0, 0, 0, 0.75);

  .el-carousel {
    margin: 0px !important;
    .el-carousel__container {
      height: 100%;
      overflow: hidden;

      .el-carousel__item {
        width: 100% !important;
        height: 100%;
        left: 0px !important;
        background-color: transparent !important;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  > div {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .cutscenes {
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 10;
    color: #ffffff;
    display: flex;
    /**/
    align-items: center;
    /**/
    justify-content: center;

    text-align: justify;
    font-size: 30px;
    background-color: #ffffff;
    // background-color: rgba(255, 0, 0, 0.5);

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .vw_1 {
    #player1 {
      width: 100%;
      height: 100%;
    }
  }

  .vw_2 {
    #player1 {
      width: 100%;
      height: 100%;
    }

    #player2 {
      width: calc(100% / 3);
      height: calc(100% / 3);
      position: absolute;
      bottom: 10px;
      right: 10px;
      border: 1px solid @BorderColor;
    }
  }

  .vw_3 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    grid-template-rows: repeat(2, calc(100% / 2));
    grid-template-areas:
      "index1 index1 index2"
      "index1 index1 index3";

    #player1 {
      border-right: 1px solid @BorderColor;
    }
    #player3 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_4 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index1"
      "index1 index1 index1"
      "index2 index3 index4";

    #player1 {
      border-bottom: 1px solid @BorderColor;
    }
    #player3 {
      border-left: 1px solid @BorderColor;
      border-right: 1px solid @BorderColor;
    }
  }

  .vw_5 {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index1 index2"
      "index1 index1 index1 index3"
      "index4 index4 index5 index5";

    #player1,
    #player4 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_6 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index2"
      "index1 index1 index3"
      "index4 index5 index6";

    #player1,
    #player4,
    #player5 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5,
    #player6 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_7 {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index1 index2"
      "index1 index1 index1 index3"
      "index4 index5 index6 index7";

    #player1,
    #player4,
    #player5,
    #player6 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5,
    #player6,
    #player7 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_8 {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4));
    grid-template-rows: repeat(4, calc(100% / 4));
    grid-template-areas:
      "index1 index1 index1 index2"
      "index1 index1 index1 index3"
      "index1 index1 index1 index4"
      "index5 index6 index7 index8";

    #player1,
    #player4,
    #player5,
    #player6,
    #player7 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5,
    #player6,
    #player7,
    #player8 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_9 {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index2 index3"
      "index1 index1 index4 index5"
      "index6 index7 index8 index9";

    #player1,
    #player2,
    #player4,
    #player5,
    #player6,
    #player7,
    #player8 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5,
    #player6,
    #player7,
    #player8,
    #player9 {
      border-top: 1px solid @BorderColor;
    }
  }

  .vw_10 {
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5));
    grid-template-rows: repeat(3, calc(100% / 3));
    grid-template-areas:
      "index1 index1 index1 index2 index3"
      "index1 index1 index1 index4 index5"
      "index6 index7 index8 index9 index10";

    #player1,
    #player2,
    #player4,
    #player5,
    #player6,
    #player7,
    #player8,
    #player9 {
      border-right: 1px solid @BorderColor;
    }
    #player3,
    #player4,
    #player5,
    #player6,
    #player7,
    #player8,
    #player9,
    #player10 {
      border-top: 1px solid @BorderColor;
    }
  }

  #player1 {
    grid-area: index1;
  }
  #player2 {
    grid-area: index2;
  }
  #player3 {
    grid-area: index3;
  }
  #player4 {
    grid-area: index4;
  }
  #player5 {
    grid-area: index5;
  }
  #player6 {
    grid-area: index6;
  }
  #player7 {
    grid-area: index7;
  }
  #player8 {
    grid-area: index8;
  }
  #player9 {
    grid-area: index9;
  }
  #player10 {
    grid-area: index10;
  }
}
</style>>